import React from 'react';
import './App.css';
import Navbar from './components/Navbar';
import Jumbotron from './components/Jumbotron';
import Services from './components/Services';
import Product from './components/Product';
import Team from './components/Team';
import Footer from './components/Footer';
import Tools from './components/Tools';

function App() {
  const [scrollPotition, setScrollPotition] = React.useState(0);
  const [screenWidth, setScreenWidth] = React.useState(window.innerWidth);

  React.useEffect(() => {
    window.addEventListener('scroll', (e) => {
      setScrollPotition(e.srcElement.documentElement.scrollTop);
    });
    window.addEventListener('resize', () => {
      setScreenWidth(window.innerWidth);
    });
  }, []);

  return (
    <div>
      <Navbar scrollPotition={scrollPotition} screenWidth={screenWidth} />
      <Jumbotron />
      <Services />
      <Product screenWidth={screenWidth} scrollPotition={scrollPotition} />
      <Tools />
      <Team />
      <Footer />
    </div>
  );
}

export default App;
